import { create } from 'zustand'
import axios from 'axios'
import { devtools } from 'zustand/middleware';

export const useDataStore = create(devtools((set) => ({
  categori: ["", "economy", "society", "culture", "world","technology" ],
  categoriId: [100, 101, 102, 103, 104, 105],
  currentCategori: 100,
  setCategori: (data) => set({ currentCategori: data }),
  topics: ['topic1', 'topic2', 'topic3', 'topic4', 'topic5']
})))