import {
  Container, useMediaQuery, makeStyles, useTheme
} from '@mui/material';
import Grid from '@mui/material/Unstable_Grid2'
import './App.css';
import Aside from "./components/Aside";
import Header from "./components/Header";
import Main from "./components/Main";
import Nav from "./components/Nav";
import Footer from "./components/Footer"
import { useDataStore } from './store/store'
import { Route, Routes } from 'react-router-dom';
import { Fragment } from 'react';
function App() {
  const { categori, categoriId } = useDataStore();
  return (
    <Fragment>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <Header />
        </Grid>
      </Grid>
      <Container fixed maxWidth="lg">

        <Grid item xs={12} sx={{ width: '100%' }}>
          <Nav />
        </Grid>

        <Grid className="body" container spacing={2} sx={{
          height: '100%',
          flexDirection: { xs: 'column-reverse', sm: 'row' }
        }}>
          <Grid item xs={12} sm={8}>
            <Routes>
              {
                (() => {
                  const arr = [];
                  for (let i = 0; i < categori.length; i++) {
                    arr.push(
                      <Route exact path={`/${categori[i]}`} element={<Main categoriId={categoriId[i]} />}></Route>
                    )
                  }
                  return arr;
                })()
              }
            </Routes>
          </Grid>
          <Grid item xs={12} sm={4}>
            <Routes>          {
              (() => {
                const arr = [];
                for (let i = 0; i < categori.length; i++) {
                  arr.push(
                    <Route exact path={`/${categori[i]}`} element={<Aside index={i} />}></Route>
                  )
                }
                return arr;
              })()
            }
            </Routes>
          </Grid>
        </Grid>

      </Container>
      <Grid container>
        <Grid item xs={12}>
          <Footer />
        </Grid>
      </Grid>
    </Fragment >
  );
}


export default App;
