import { Grid } from '@mui/material';
import React from 'react';
import { useDataStore } from '../store/store';
function Header() {
    const {setCategori} = useDataStore();
    return (
        <Grid container style={{ cursor: 'pointer' }} spacing={0}>
            <Grid item xs={12} className="app-header" onClick={() => {
                window.location.replace('/')
                setCategori(100);
            }}>
                <h4>네이버 뉴스 요약기</h4>
            </Grid>
        </Grid>
    )
}
export default Header;  